#single-news {
	.x-img-main {
		@include lg {
			display: block;
			width: 100%;
			object-fit: cover; } }
	h2 {
		@include sm {
			font-size: 20px;
			margin-bottom: 10px; } }
	h3 {
		margin-top: 15px;
		margin-bottom: 10px;
		font-size: 20px; }
	iframe {
		@include sm {
			width: 100%; } }
	p {
		img {
			display: block;
			width: 100%;
			object-fit: cover;
			@include md {
				height: 300px; } } } }

.gallery {
	.img-block:first-child, .img-block:nth-child(5) {
		margin-left: 10px; } }
