.center-movie {
	@include lg {
		width: 28.54%;
		margin-left: 0;
		padding-left: 40px;
		height: 100%; }
	@include md {
		float: none;
		width: 100%;
		padding: 0; }
	a {
		@include lg {
			position: relative;
			width: 100%;
			bottom: 5px; } }
	a:last-child {
		@include lg {
			position: relative; } } }
.right-movie {
	@include lg {
		width: 34.45%;
		margin-left: 0;
		padding-left: 20px;
		height: 100%; }
	@include md {
		float: none;
		padding: 0;
		width: 100%; }
	a:last-child {
		@include lg {
			display: block;
			width: 100%;

			position: relative; } } }
#movies {
	.btn-movie {
		@include lg {
			width: 100%;
			padding: 28px 20px;
			line-height: 18px;
			font-size: 18px; }
		@include sm {
			padding: 14px 10px;
			font-size: 16px; } } }

.movie-wrap {
	@include sm {
		margin-bottom: 15px; }
	a:first-child {
		@include lg {
			display: block;
			width: 30.31%; }
		@include sm {
			width: 50%; } }
	a {
		img {
			@include lg {
				width: 100%;
				display: block; }
			@include md {
				float: none; } } }
	.btn-trailer {
		@include lg {
			font-size: 15px;
			width: 100%;
			padding: 9px; }
		@include md {
			margin-top: 15px; } } }
.movie-descr {
	p {
		@include md {
			margin-top: 25px; } } }
.movie-genre {
	p {
		@include md {
			margin-top: 20px;
			margin-bottom: 30px; } } }
.right-movie {
	h4 {
		@include md {
			margin-top: 10px;
			margin-bottom: 10px; } }
	p {
		@include md {
			margin-bottom: 10px; } } }

.movie-wrap {
	img {
		@include lg {
			width: 30.31%; }
		@include md {
			float: none; }
		@include sm {
			width: 50%; } } }
