.menu-mobile-cross {
  display: none;
}

@media only screen and (max-width: 1023px) {
  .menu-mobile-cross {
    display: inline-block;
    position: absolute;
    top: 14px;
    right: 15px;
    width: 33px;
    height: 33px;
  }
  .menu-mobile-cross.is-open:before {
    transform: rotate(-45deg);
    top: 10px;
    background-color: #fff;
  }
  .menu-mobile-cross.is-open:after {
    top: 10px;
    transform: rotate(45deg);
    background-color: #fff;
  }
  .menu-mobile-cross.is-open span {
    top: 10px;
    opacity: 0;
    top: 0;
    background-color: #fff;
  }
  .menu-mobile-cross span {
    position: absolute;
    top: 10px;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: opacity 0.3s;
  }
  .menu-mobile-cross span.is-open {
    transform: rotate(-45deg);
    background-color: #fff;
  }
  .menu-mobile-cross:before {
    content: '';
    position: absolute;
    top: 0;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: transform 0.5s;
  }
  .is-open .menu-mobile-cross:before {
    transform: rotate(45deg);
    top: 10px;
    background-color: #fff;
  }
  .menu-mobile-cross:after {
    content: '';
    position: absolute;
    top: 21px;
    width: 33px;
    height: 3px;
    margin-bottom: 10px;
    background-color: #fff;
    cursor: pointer;
    transition: transform 0.5s;
  }
  .is-open .menu-mobile-cross:after {
    opacity: 0;
    top: 0;
    background-color: #fff;
  }
}

