footer {
	.container-new {
		@include md {
			padding: 45px 45px; }
		@include sm {
			padding: 15px; } }
	h2 {
		@include md {
			margin-bottom: 15px;
			font-size: 16px; } }
	.row {
		@include lg {
			display: flex;
			justify-content: space-between; }
		@include md {
			display: block; }
		&:before {
			@include lg {
				display: none; } }
		&:after {
			@include lg {
				display: none; } } }
	.footer-c {
		@include lg {
			margin-left: 0;
			float: none; }
		@include md {
			margin-bottom: 25px; } }
	.footer-l {
		@include lg {
			float: none; }
		@include md {
			margin-bottom: 25px; } }
	.footer-r {
		@include lg {
			float: none; }
		@include md {
			margin-bottom: 25px; } } }

#copy {
	@include md {
		height: 25px; }
	p {
		@include md {
			line-height: 25px;
			font-size: 12px; } } }


