.movie-block {
	height: 100%;
	margin: 0 21px; }
.movie-block:last-child {
	@include md {
		margin-right: 21px; } }
.movie-block:first-child {
	@include md {
		margin-left: 21px; } }
.movie-block p {
	margin-bottom: 30px;
	@include md {
		font-size: 14px; } }
.container-p {
	@include md {
		padding: 0 45px; }
	@include sm {
		padding: 15px; } }
#movies .container-new {
	@include md {
		padding-bottom: 25px; } }
