#news {
	.container-new {
		@include md {
			padding: 0 45px 35px; }
		@include sm {
			padding: 0 15px 35px; } } }
// .container-p
// 	+md
// 		padding: 0 45px
// 	+sm
// 		padding: 0 15px 35px

.big-row {
	@include md {
		margin-bottom: 0; }
	.news-left {
		width: calc(100% - 341px);
		@include md {
			width: 100%;
			max-width: 100%;
			margin-bottom: 10px; }
		@include sm {
			height: 400px; }
		@include r(450) {
			height: 300px; } }
	.news-right {
		@include md {
			display: flex;
			width: 100%;
			flex-direction: column; } }
	.news-right-top {
		@include md {
			width: 100%;
 }			// margin-bottom: 10px
		@include r(450) {
			height: 200px; } }
	.news-right-bottom {
		@include md {
			width: 100%;
			margin-bottom: 10px; }
		@include sm {
			padding: 5px; }
		h3 {
			@include sm {
				font-size: 16px; } } }
	.xoverlay-data {
		@include sm {
			padding: 10px 15px; }
		h3 {
			@include sm {
				font-size: 16px; } } } }
.big-row {
	@include md {
		margin-bottom: 0; }
	.news-right-top {
		img {
			object-fit: cover; } } }
.x-right {
	.xoverlay-box {
		right: -100%; } }
.xoverlay {
	@include sm {
		.x-img-main {
			height: 400px; } }
	@include r(450) {
		.x-img-main {
			height: 300px; } } }

.small-row {
	@include md {
		margin-bottom: 0;
		display: flex;
		flex-direction: column-reverse; }
	.news-left {
		@include md {
			width: 100%;
			margin-bottom: 10px; }
		@include sm {
			padding: 5px; }
		h3 {
			font-size: 16px; } }
	.news-right {
		width: calc(100% - 354px);
		@include md {
			width: 100%;
			margin-bottom: 10px; }
		@include r(450) {
			height: 200px; }
		img {
			object-fit: cover; } } }
.btn-news {
	@include lg {
		max-width: 390px;
		width: 100%;
		padding: 26px 10px;
		line-height: 1.1; }
	@include sm {
		width: 100%;
		font-size: 16px;
		padding: 13px 10px; } }

.btn-prev {
	@include md {
		margin: 15px 0 0; } }

.btn-next {
	@include md {
		margin-top: 15px; } }


