
div.news-detail {
	img.detail_picture {
		@include md {
			float: none; } } }

.news-detail {
	h3 {
		@include sm {
			font-size: 20px; } }
	table {
		a {
			font-size: 14px; } }
	p {
		i {
			font-size: 14px; } } }
.prod-who {
	p {
		font-size: 15px; } }

.news-date-time {
	font-size: 14px;
	@include md {
		display: block; } }

#about-prod {
	@include md {
		table {
			width: 100%;
			& td:first-child {
				width: 50%;
				@include sm {
					display: block; } }
			& td:last-child {
				width: 50%;
				@include sm {
					display: block; } } } }
	.gallery {
		// font-size: 0
		@include lg {
			text-align: center; } }
	.detail_picture {
		@include md {
			margin-right: 0; }
		@include sm {
			max-width: 296px;
			object-fit: cover;
			width: 100%; } } }

#about > .container-new > p > a {
	@include sm {
		padding: 13px 45px;
		font-size: 16px;
		display: block;
		width: 100%; } }

#about > .container-new > p {
	@include sm {
		width: 100%; } }
