#about {
	.container-new {
		font-size: 0;
		@include sm {
			padding: 0 15px 35px; } }
	h2 {
		@include sm {
			font-size: 20px; } }
	hr {
		@include sm {
			margin-top: 20px; } } }

hr {
	margin-bottom: 10px; }
.about-block {
	img {
		@include lg {
			width: 31%; }
		@include sm {
			width: 40%;
			float: none; } } }
.about-left {
	@include lg {
		width: 69%;
		margin-left: 0;
		padding-left: 45px; }
	@include sm {
		width: 100%;
		float: none;
		padding-left: 0; }
	p {
		@include sm {
			font-size: 16px;
			margin-bottom: 15px; } } }
.prod-block {
	vertical-align: top;
	width: 33%;
	padding: 1.5%;
	margin: 0;
	padding: 1.5%;
	vertical-align: top;
	@include md {
		width: 50%; }
	@include sm {
		width: 100%; }
	img {
		display: block;
		width: 100%;
		height: 436px;
		object-fit: cover;
		@include md {
			object-position: top; } }
	p {
		margin-bottom: 15px;
		margin-top: 7px;
		font-size: 14px; } }
